.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

.header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.p {
    background-color: white;
    color: black;
    padding: 2px;
    border-radius: 10%;
    font-size: medium;
}

.Unity {
    width: 94vw;
}

.centeredDiv {
    position: absolute;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    width: 90px;
    bottom: 10%;
}

